.BadgeDetails__hero {
  width: 100%;
  padding: 2rem 0;
  background: url('../../images/stars.svg'), #1b1b25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
}

.BadgeDetails__hero-attendant-name {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.BadgeDetails__buttons {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  justify-content: center;
}

.BadgeDetails__buttons > button {
  margin-top: 1em;
}
